<template>
  <section style="max-width:900px;margin:auto;padding:1rem;">
    <header style="display:flex;justify-content:space-between;align-items:center;">
      <h1>Usuarios</h1>
      <button @click="$emit('volver')">‚Üê Volver</button>
    </header>

    <!-- Formulario de creaci√≥n de usuario -->
    <div style="margin:.75rem 0;border:1px solid #ddd;padding:1rem;border-radius:12px;">
      <h3 style="margin:0 0 .5rem 0;">Crear usuario</h3>
      <form @submit.prevent="onCreate" style="display:flex;gap:.5rem;flex-wrap:wrap;">
        <input v-model="form.firstName" placeholder="Nombre" required />
        <input v-model="form.lastName"  placeholder="Apellidos" required />
        <input v-model.number="form.age" type="number" min="0" placeholder="Edad" required />
        <input v-model="form.email" type="email" placeholder="Email" required />
        <button :disabled="creating" type="submit">{{ creating ? 'Creando...' : 'Crear' }}</button>
      </form>
    </div>

    <p v-if="loading">Cargando‚Ä¶</p>
    <p v-if="error" style="color:#b00;">{{ error }}</p>

    <!-- Lista de usuarios -->
    <ul>
      <li v-for="u in users" :key="u.id" style="margin:.35rem 0;">
        <strong>{{ u.firstName }} {{ u.lastName }}</strong> ‚Äî {{ u.email }}
        <button @click="editUser(u)">Editar</button>
        <button @click="deleteUser(u.id)">Eliminar</button>
      </li>
    </ul>

    <!-- Paginaci√≥n -->
    <div style="display:flex;align-items:center;gap:.5rem;margin-top:.75rem;">
      <button :disabled="!canPrev()" @click="prev">Anterior</button>
      <button :disabled="!canNext()" @click="next">Siguiente</button>
      <span>Mostrando {{ users.length }} de {{ total }}</span>
    </div>
  </section>
</template>

<script>
import { fetchUsers, addUser, deleteUser, updateUser } from '../api/users';

export default {
  name: 'UsersPage',
  data() {
    return {
      users: [],
      total: 0,
      limit: 10,
      skip: 0,
      loading: false,
      error: '',
      form: { firstName: '', lastName: '', age: 18, email: '' },
      creating: false,
      editingUser: null,  // Para editar un usuario
    };
  },
  async created() {
    await this.loadUsers();
  },
  methods: {
    async loadUsers() {
      try {
        this.loading = true;
        this.error = '';
        const data = await fetchUsers({ limit: this.limit, skip: this.skip });
        this.users = data.users;
        this.total = data.total;
      } catch (e) {
        this.error = 'Error cargando usuarios';
        console.error(e);
      } finally {
        this.loading = false;
      }
    },
    canPrev() { return this.skip > 0; },
    canNext() { return this.skip + this.limit < this.total; },
    async next() { if (this.canNext()) { this.skip += this.limit; await this.loadUsers(); } },
    async prev() { if (this.canPrev()) { this.skip -= this.limit; await this.loadUsers(); } },

    // M√©todo para crear un usuario
    async onCreate() {
      try {
        this.creating = true;
        const newUser = await addUser(this.form);
        this.form = { firstName: '', lastName: '', age: 18, email: '' };  // Resetea el formulario
        this.users.push(newUser);  // A√±ade el nuevo usuario a la lista local de usuarios
        this.skip = 0;  // Resetea la paginaci√≥n si lo deseas
      } finally {
        this.creating = false;
      }
    },

    // M√©todo para editar un usuario
    editUser(user) {
      this.editingUser = user;  // Carga el usuario en el formulario
      this.form.firstName = user.firstName;
      this.form.lastName = user.lastName;
      this.form.age = user.age;
      this.form.email = user.email;
    },

    // M√©todo para actualizar el usuario
    async onEdit() {
      try {
        this.creating = true;
        const updatedUser = await updateUser(this.editingUser.id, this.form); // Actualiza el usuario
        this.users = this.users.map(u => (u.id === updatedUser.id ? updatedUser : u));  // Actualiza la lista local
        this.editingUser = null;  // Limpiar el usuario editado
        this.form = { firstName: '', lastName: '', age: 18, email: '' };
      } finally {
        this.creating = false;
      }
    },

    // M√©todo para eliminar un usuario
    async deleteUser(userId) {
      if (confirm("¬øEst√°s seguro de eliminar este usuario?")) {
        try {
          await deleteUser(userId);
          alert("Usuario eliminado!");
          this.loadUsers(); // Vuelve a cargar la lista de usuarios despu√©s de eliminar
        } catch (error) {
          alert("Error al eliminar el usuario: " + error.message);
        }
      }
    },
  },
};
</script>
