<template>
  <div>
    <!-- Vista Portal de Videojuegos -->
    <PortalDeVideojuegos
      v-if="vista === 'portal'"
      @open-tres="vista = 'tres'"
      @open-usuarios="vista = 'users'" 
      msg="Portal De Videojuegos"
    />

    <!-- Vista Tres en Raya -->
    <TresEnRaya v-else-if="vista === 'tres'" @volver="vista = 'portal'" />

    <!-- Vista Usuarios -->
    <UsersPage v-else-if="vista === 'users'" @volver="vista = 'portal'" @user-updated="refreshUsers" />
  </div>
</template>

<script>
import PortalDeVideojuegos from './components/PortalDeVideojuegos.vue';
import TresEnRaya from './components/TresEnRaya.vue';
import UsersPage from './components/UsersPage.vue';

export default {
  name: 'App',
  components: { PortalDeVideojuegos, TresEnRaya, UsersPage },
  data() {
    return {
      vista: 'portal',  
    };
  },
  methods: {
    
    refreshUsers() {
      
      this.$refs.usersPage.loadUsers();  
    }
  }
}
</script>

