// src/api/users.js
import http from './http';

// Lista de usuarios
export async function fetchUsers({ limit = 10, skip = 0 } = {}) {
  const { data } = await http.get('/users', { params: { limit, skip } });
  return data; // { users, total, skip, limit }
}

// Crear usuario (simulado por dummyjson)
export async function addUser(user) {
  const { data } = await http.post('/users/add', user, {
    headers: { 'Content-Type': 'application/json' },
  });
  return data;
}