<template>
  <div class="tres-wrapper">
    <header class="topbar">
      <button class="volver" type="button" @click="$emit('volver')">‚Üê Volver</button>
      <h2>Tres en Raya</h2>
    </header>

    <div class="status">
      <span v-if="winner && winner !== 'Empate'">Ganador: {{ winner }}</span>
      <span v-else-if="winner === 'Empate'">Empate</span>
      <span v-else>Turno: {{ current }}</span>
    </div>

    <div class="board" role="grid" aria-label="Tablero tres en raya">
      <button
        v-for="(cell, i) in board"
        :key="i"
        class="cell"
        role="gridcell"
        :aria-label="ariaLabel(i)"
        :disabled="!!cell || !!winner"
        @click="play(i)"
        @keydown.enter.prevent="play(i)"
        @keydown.space.prevent="play(i)"
      >
        {{ cell }}
      </button>
    </div>

    <div class="controls">
      <button class="primary" type="button" @click="reset">Reiniciar</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TresEnRaya',
  data() {
    return {
      board: Array(9).fill(null),
      current: 'X',
      winner: null, // 'X' | 'O' | 'Empate' | null
      moves: 0
    }
  },
  methods: {
    play(i) {
      if (this.board[i] || this.winner) return
      this.$set ? this.$set(this.board, i, this.current) : (this.board[i] = this.current)
      this.moves++

      const w = this.checkWin()
      if (w) {
        this.winner = w
        return
      }
      if (this.moves === 9) {
        this.winner = 'Empate'
        return
      }
      this.current = this.current === 'X' ? 'O' : 'X'
    },
    checkWin() {
      const b = this.board
      const lines = [
        [0,1,2],[3,4,5],[6,7,8], // filas
        [0,3,6],[1,4,7],[2,5,8], // columnas
        [0,4,8],[2,4,6]          // diagonales
      ]
      for (const [a,bi,c] of lines) {
        if (b[a] && b[a] === b[bi] && b[a] === b[c]) return b[a]
      }
      return null
    },
    reset() {
      this.board = Array(9).fill(null)
      this.current = 'X'
      this.winner = null
      this.moves = 0
    },
    ariaLabel(i) {
      const r = Math.floor(i/3)+1, c = (i%3)+1
      const val = this.board[i] || 'vac√≠a'
      return `Fila ${r}, Columna ${c}, ${val}`
    }
  }
}
</script>

