  margin: 14px 0 18px;
  font-weight: 600;
  letter-spacing: .2px;
}

.board {
  display: grid;
  grid-template-columns: repeat(3, 110px);
  grid-template-rows: repeat(3, 110px);
  gap: 10px;
  justify-content: center;
}
